// @flow

/**
 * currentUsers.js - Редюсер для изменения данных о количестве пользователей в хранилище
 * @module reducers/currentUsers
 * @requires module:constants/CurrentUsers
 * */

import {
SET_CURRENT_USERS,
SET_AVG_VIEWTIME,
SET_RECIRCULATION,
} from '../constants/CurrentUsers'

/**
 * @memberof reducers/currentUsers
 * */
type State = {
    count: number,
    viewtime: number,
    recirculation: number,
}

/**
 * @memberof reducers/currentUsers
 * */
type Action = {
    type: string,
    count: number,
    viewtime: number,
    recirculation: number,
}

/**
 * Начальное состояние
 * @memberof reducers/currentUsers
 * */
export const initialState: State = {
    count: 0,
    viewtime: 0,
    recirculation: 0,
}

/**
 * Возвращает новое состояние с количеством пользователей
 * @memberof reducers/currentUsers
 * @function currentUsers
 * @param {State} state
 * @param {Action} action
 * @return {State}
 * */
export default function currentUsers(state: State = initialState, action: Action): State {
    switch (action.type) {
        case SET_CURRENT_USERS:
            return {...state, count: action.count}
    
        case SET_AVG_VIEWTIME:
            return {...state, viewtime: action.viewtime}
    
        case SET_RECIRCULATION:
            return {...state, recirculation: action.recirculation}
        
        default:
            return state;
    }
}



