// @flow

/**
 * topArticles.js - Редьюсер для изменения количества просмотров, заголовков и URL-ов TOP статей в хранилище
 * @module reducers/topArticles
 * @requires module:constants/TopArticles
 * */

import {SET_TOP_ARTICLES} from '../constants/TopArticles'

/**
 * @memberof reducers/topArticles
 * */
type TopArticles = [{
    value: number,
    title: string,
    url: string,
    percent: number
}]

/**
 * @memberof reducers/topArticles
 * */
type State = {
    topArticles: TopArticles
}

/**
 * @memberof reducers/topArticles
 * */
type Action = {
    type: string,
    topArticles: TopArticles
}

/**
 * Начальное состояние
 * @memberof reducers/topArticles
 * */
export const initialState: State = {
    topArticles: [{
        value: 0,
            title: '',
            url: '',
            percent: 0
        }]
}

/**
 * Возвращает новое состояние с количеством просмотров, заголовками и URL-ами TOP статей
 * @memberof reducers/topArticles
 * @function topArticles
 * @param {State} state
 * @param {Action} action
 * @return {State}
 * */
export default function topArticles(state: State = initialState, action: Action): State {
    switch (action.type) {
        case SET_TOP_ARTICLES:
            return {...state, topArticles: [...action.topArticles]}

        default:
            return state;
    }
}



