// @flow

/**
 * devices.js - Редьюсер для изменения информации об устройствах
 * @module reducers/devices
 * @requires module:constants/Devices
 * */

import {SET_DEVICES} from '../constants/Devices'

/**
 * @memberof reducers/devices
 * */
type Devices = [{
    value: number,
    title: string,
    percent: number,
    url: string,
}]

/**
 * @memberof reducers/devices
 * */
type State = Devices

/**
 * @memberof reducers/devices
 * */
type Action = {
    type: string,
    devices: Devices
}

/**
 * Начальное состояние
 * @memberof reducers/devices
 * */
export const initialState: State = [{
    value: 0,
    title: '',
    percent: 0,
    url: '',
}]

/**
 * Возвращает новое состояние с данными об устройствах
 * @memberof reducers/devices
 * @function devices
 * @param {State} state
 * @param {Action} action
 * @return {State}
 * */
export default function devices(state: State = initialState, action: Action): State {
    switch (action.type) {
        case SET_DEVICES:
            return [...action.devices]

        default:
            return state;
    }
}



