// @flow

/**
 * topReferers.js - Редьюсер для изменения количества просмотров, заголовков и URL-ов TOP статей в хранилище
 * @module reducers/topReferers
 * @requires module:constants/TopReferers
 * */

import {SET_TOP_REFERERS} from '../constants/TopReferers'

/**
 * @memberof reducers/topReferers
 * */
type TopReferers = [{
    value: number,
    title: string,
    url: string,
    percent: number
}]

/**
 * @memberof reducers/topReferers
 * */
type State = {
    topReferers: TopReferers
}

/**
 * @memberof reducers/topReferers
 * */
type Action = {
    type: string,
    topReferers: TopReferers
}

/**
 * Начальное состояние
 * @memberof reducers/topReferers
 * */
export const initialState: State = {
    topReferers: [{
        value: 0,
        title: '',
        url: '',
        percent: 0
    }]
}

/**
 * Возвращает новое состояние с данными о топе источников
 * @memberof reducers/topReferers
 * @function topReferers
 * @param {State} state
 * @param {Action} action
 * @return {State}
 * */
export default function topReferers(state: State = initialState, action: Action): State {
    switch (action.type) {
        case SET_TOP_REFERERS:
            return {...state, topReferers: [...action.topReferers]}

        default:
            return state;
    }
}



